.stay-details {

    .details-header {
        margin-block: 1.2em;
        align-items: flex-end;

        .content {

            h2 {
                margin-block-end: .3em;
            }

            .inner-content {

                .rating-container {
                    font-family: Circular-Light, sans-serif;

                    img {
                        width: 1em;
                        margin-inline-end: 0.3em;
                        margin-block-end: 0.2em;
                    }

                    span {
                        margin-inline-end: 0.2em;
                    }
                }

                p {
                    color: $clr2;
                }
            }
        }

        .share-save-container {

            a {
                color: $clr2;
                text-decoration: underline;
                font-size: em(15px);

                .share-container {
                    margin-inline-end: 1em;

                    img {
                        width: 1.1em;
                        margin-inline-end: .3em;
                    }

                }

                .like-container {


                    img {
                        width: 1.1em;
                        margin-inline-end: .3em;
                    }
                }
            }


        }
    }

    .details-gallery {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-template-rows: 25vh 25vh;
        grid-gap: em(8px);
        margin-block-end: 1em;


        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            aspect-ratio: 1/1;
        }

        img:first-child {
            grid-column: 1 / span 2;
            grid-row: 1 / span 2;
            border-top-left-radius: .8em;
            border-bottom-left-radius: .8em;
        }

        img:nth-child(3) {
            border-top-right-radius: .8em;
        }

        img:nth-child(5) {
            border-bottom-right-radius: .8em;
        }
    }

    .stay-details-content {

        display: grid;
        grid-template-columns: 1.8fr 1fr;
        column-gap: 5em;

        .stay-details-content-column1 {
            grid-column: 1;

            .stay-extra-details {

                .stay-host-by {
                    margin-block-start: 2em;
                    margin-block-end: 1.2em;

                    img {
                        width: 3em;
                        border-radius: 50%;
                        aspect-ratio: 1/1;
                        object-fit: cover;
                    }
                }

                .main-amenities {
                    height: em(70px);
                    line-height: 1.3;
                    font-family: Circular-Light, sans-serif;

                    .main-amenity {
                        border: 1px solid $clr4;
                        border-radius: .8em;
                        padding-inline: 1.8em;
                        margin-inline-end: 1em;
                        height: 100%;

                        img {
                            width: 1.5em;
                            margin-inline-end: 1em;
                        }
                    }
                }

                .secondary-amenities {

                    border-top: 0.5px solid $clr4;
                    border-bottom: 0.5px solid $clr4;
                    margin-block: 1.5em;
                    padding-block-start: 2em;
                    padding-block-end: 1.2em;

                    .secondary-amenity-container {
                        margin-block-end: 1em;

                        img {
                            width: 1.6em;
                            margin-inline-end: 1em;
                        }

                        .secondary-amenity-content-container {
                            p {
                                font-family: Circular-Light;
                                color: $clr7;
                            }
                        }
                    }
                }

                .summary {
                    border-block-end: 1px solid $clr4;
                    padding-block-start: 1em;
                    padding-block-end: 2em;
                }
            }

            .stay-amenities {
                border-block-end: 1px solid $clr4;
                padding-block: 2em;
                margin-block-end: 3em;

                .amenitis-content {
                    display: grid;
                    grid-template-columns: 1fr 1fr;
                }

                h2 {
                    margin-block-end: 1em;
                }

                .amenty-container {
                    margin-block-end: 1em;

                    img {
                        width: 1.5em;
                        margin-inline-end: 1em;
                    }
                }
            }

            .calendar-picker-header {

                .rdp {
                    margin: 0;

                    .rdp-months {
                        display: flex;
                        justify-content: space-between;
                    }
                }
            }
        }

        .stay-details-content-column2 {
            grid-column: 2;
            padding-block-start: 2em;

            .order-form-container {
                position: sticky;
                top: 8em;
                grid-column: 2;
                grid-row: 1;
                justify-self: end;
                width: 100%;
                border: 1px solid $clr4;
                border-radius: 1em;
                box-shadow: rgba(0, 0, 0, 0.12) 0px 6px 16px;
                display: flex;
                flex-direction: column;
                padding: 2em;
                align-items: center;

                >* {
                    width: 100%;
                }

                .price-rating-container {
                    margin-block-end: 0.8em;

                    .price-container {
                        h2 {
                            margin-inline-end: 0.3em;
                        }
                    }

                    .rating-review-container {
                        .rating-container {
                            margin-inline-end: 0.2em;

                            img {
                                width: 1em;
                                margin-inline-end: 0.3em;
                                margin-block-end: .2em;
                            }
                        }
                    }
                }

                form {
                    >* {
                        width: 100%;
                    }

                    .dates-guests-container {
                        width: 100%;


                        .dates-container {
                            width: 100%;
                            height: 100%;
                            border: 1px solid rgba(0, 0, 0, 0.356);
                            border-top-left-radius: 0.5em;
                            border-top-right-radius: 0.5em;
                            border-block-end: 0;

                            .check-in-container {
                                border-inline-end: 1px solid rgba(0, 0, 0, 0.356);
                                width: 50%;
                                height: 100%;
                                padding-inline: .8em;
                                padding-block-start: .3em;
                                padding-block-end: .5em;

                                span {
                                    text-transform: uppercase;
                                    font-size: em(9px);
                                    font-family: Circular-Medium, sans-serif;
                                }

                                .date-check-in {
                                    font-family: Circular-Light, sans-serif;
                                    font-size: em(15px);
                                }

                                &:hover {
                                    cursor: pointer;
                                }
                            }

                            .check-out-container {
                                width: 50%;
                                height: 100%;
                                padding-inline: .8em;
                                padding-block-start: .3em;
                                padding-block-end: .5em;

                                span {
                                    text-transform: uppercase;
                                    font-size: em(9px);
                                    font-family: Circular-Medium, sans-serif;
                                }

                                .date-check-out {
                                    font-family: Circular-Light, sans-serif;
                                    font-size: em(15px);
                                }

                                &:hover {
                                    cursor: pointer;
                                }
                            }
                        }

                        .guests {
                            border: 1px solid rgba(0, 0, 0, 0.356);
                            border-bottom-left-radius: 0.5em;
                            border-bottom-right-radius: 0.5em;
                            width: 100%;
                            padding-inline: .8em;
                            padding-block-start: .8em;
                            padding-block-end: .5em;
                            text-align: left;

                            span {
                                text-transform: uppercase;
                                font-size: em(9px);
                                font-family: Circular-Medium, sans-serif;
                                margin-block-end: .3em;
                            }

                            .guests-count {
                                font-family: Circular-Light, sans-serif;
                                font-size: em(15px);
                            }

                            &:hover {
                                cursor: pointer;
                            }
                        }
                    }

                    .date-picker-container {
                        width: 100%;

                        .rdrDateRangePickerWrapper {
                            width: 100%;

                            >* {
                                width: 100%;
                            }

                            .rdrDefinedRangesWrapper {
                                display: none;
                            }

                            .rdrMonths {
                                display: flex;
                                flex-direction: row;
                                width: 100%;
                            }

                            .rdrCalendarWrapper {
                                >* {
                                    width: 100%;
                                }

                                .rdrDateDisplayWrapper {
                                    >* {
                                        width: 100%;
                                    }
                                }

                                .rdrMonthAndYearWrapper {
                                    width: 100%;
                                }

                                .rdrMonths {
                                    >* {
                                        width: 100%;
                                    }
                                }
                            }
                        }
                    }
                }

                .txt-charged {
                    text-align: center;
                    margin-block: 1.5em;
                }

                .nights-sum {
                    // margin-block: 2em;
                    padding-block-start: 1em;
                    border-block-start: 1px solid rgba(0, 0, 0, 0.356);

                    .total-price-container {
                        h3 {
                            text-transform: capitalize;
                            margin-inline-end: .5em;
                        }
                    }
                }
            }
        }
    }

    .review-container {
        border-block-start: 1px solid $clr4;
        border-block-end: 1px solid $clr4;
        margin-block-start: 3em;
        margin-block-end: 3.5em;
        padding-block-start: 3em;
        padding-block-end: 3em;

        .rating-container {

            img {
                width: 1em;
                margin-inline-end: 0.5em;
            }

            h2 {
                margin: 0;
            }
        }

        .review-content {
            display: grid;
            grid-template-columns: 1fr 1fr;
            column-gap: 10em;
            margin-block-start: 2em;

            .review {
                margin-block-end: 2.5em;

                .user-details {
                    margin-block-end: 1em;

                    img {
                        width: 2.5em;
                        margin-inline-end: 0.5em;
                        border-radius: 50%;
                        aspect-ratio: 1/1;
                        object-fit: cover;
                    }
                }

                .substringed-txt-container {

                    .showmore-container {
                        margin-block-start: .8em;

                        .btn-showmore {
                            padding: 0;
                            background-color: transparent;
                            color: $clr2;
                            text-decoration: underline;
                            font-family: Circular-Medium, sans-serif;
                            margin-inline-end: 0.2em;
                        }

                        img {
                            width: 0.8em;
                            margin-block-start: 0.2em;
                            cursor: pointer;
                        }
                    }
                }
            }

            .btn-show-all-reviews {
                width: max-content;
                background-color: transparent;
                color: $clr2;
                font-family: Circular-Medium, sans-serif;
                border: 1px solid $clr2;
                padding-inline: 1.4em;
            }
        }
    }
}